.education {
    padding: 20px;
    display: flex;
    flex-wrap: wrap;
    gap: 20px;
  }
  
  .education h1 {
    width: 100%;
    margin-left: 15px;
  }
  
  .sub-education {
    background-color: #262626;
    box-shadow: rgba(0, 0, 0, 0.25) 0px 14px 28px, rgba(0, 0, 0, 0.22) 0px 10px 10px;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    overflow: hidden;
    width: 235px;
    height: 225px;
    margin: 10px 0;
    transition: transform 0.2s ease, box-shadow 0.2s ease;
    overflow: scroll;
  }

  
  .sub-education .img {
    width: 100%;
    height: 110px;
    width: 200px;
    display: block;
  }
  
  .sub-education .content {
    padding: 15px;
  }
  
  .sub-education h3 {
    margin-top: 10px;
    font-size: 18px;
    color: #FFFFFF;
    margin-left: 15px;
  }
  
  .sub-education h4 {
    margin-left: 15px;
    font-size: 16px;
    color: #ffffffb7; 
    margin-top: -15px;

  }
  
  .sub-education::-webkit-scrollbar{

    display: none;

  }