.skill {
    padding: 20px;
    display: flex;
    flex-wrap: wrap;
    gap: 20px;
  }
  
  .skill h1 {
    width: 100%;
    margin-left: 15px;
  }
  
  .sub-skill {
    background-color: #292929;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    overflow: hidden;
    width: 235px;
    height: 225px;
    margin: 10px 0;
    transition: transform 0.2s ease, box-shadow 0.2s ease;
  }

  
.img {
    background-color: transparent;
    width: 100%;
    height: 110px;
    width: 200px;
    display: block;
    margin-top: 15px;
    margin-left: 15px;
  }
  
  .sub-skill .content {
    padding: 15px;
  }
  
  
  .sub-skill h4 {
    margin-left: 15px;
    font-size: 16px;
    color: #FFFFFF; 
    margin-top: 10px;

  }

.progress{

    width: 200px;
    background-color: rgb(53, 50, 50);
    height: 24px;
    margin-left: 15px;
    border-radius: 20px;

}

.progress-bar{

    display: grid;
    background-color: rgb(255, 255, 255);
    height: 24px;
    align-items: center;
    text-align: right;
    color: #292929;
    border-radius: 20px;
}