.SideBar{

    height: 400px;
    width: 250px;
    background-color: #616161;
    position: relative;
    top: 400px;
    left: 50px;
    text-align: center;
    border-radius: 15px;

}

ul{
    list-style-type: none;
    margin: 0;
    margin-top: 20px;
    font-weight: bolder;
    padding: 0;
    font-size: large;
    font-family: Arial, Helvetica, sans-serif;
    padding-top: 20px;
}

.SideBar ul li {
    cursor: pointer;
  }
  
 ul li.selected {
    background-color: #292929; 
    padding: 8px;
  }
  