.experience {
    padding: 20px;
    display: flex;
    flex-wrap: wrap;
    gap: 20px;

  }
  
  .experience h1 {
    width: 100%;
    margin-left: 15px;
  }
  
  .Card {
    background-color: #292929;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    overflow: hidden;
    width: 225px;
    height: 225px;
    margin: 10px 0;
    transition: transform 0.2s ease, box-shadow 0.2s ease;
    
  }

  
  .Card .img {
    width: 100%;
    height: 110px;
    width: 200px;
    display: block;
  }
  
  .Card .content {
    padding: 15px;
  }
  
  .Card h3 {
    margin-top: 10px;
    font-size: 18px;
    color: #ffffff;
    margin-left: 15px;
  }
  
  .Card h4 {
    margin-left: 15px;
    font-size: 16px;
    color: #c4c2c2; 
    margin-top: -15px;

  }
  
  *{

    font-family: Arial, Helvetica, sans-serif;

  }