.project{

    padding: 20px;
    display: flex;
    flex-wrap: wrap;
    gap: 20px;

}


.project-card{

    height: 285px;
    width: 920px;
    margin-left: 15px;
    background-color: #262626;
    box-shadow: rgba(0, 0, 0, 0.25) 0px 14px 28px, rgba(0, 0, 0, 0.22) 0px 10px 10px;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    overflow: hidden;
    transition: transform 0.2s ease, box-shadow 0.2s ease;
    overflow: scroll;
    position: relative;
    top: -25px;


}

.project-h3{

    margin-left: 15px;
    margin-top: 15px;
    padding-top: 15px;

}

.project-img{

    width: 100%;
    height: 110px;
    width: 200px;
    display: block;
    margin-left: 15px;
    background-size: cover;
    overflow: hidden;

}

.project-p{

    margin-left: 15px;
    overflow: hidden;

}

.project-btn-1{

    height: 30px;
    width: 30px;
    border-radius: 10px;
    background-color: #ffffffa3;
    left: 0px;
    margin-left: 15px;
    margin-top: 0px;
    position: relative;
    background-image: url('../../../public/photo/github.png');
    background-size: cover;
    

}

.hashtag{

    margin-left: 15px;
    overflow: hidden;

}

.project-card::-webkit-scrollbar{

    display: none;

}